.container{
    margin: 1% 0 ;
}

.card{
    margin: 0 3% !important;
}
.infected{
    border-bottom: 6px solid rgba(0, 4, 255, 0.5);
}
.recovered{
    border-bottom: 6px solid rgba(0, 255, 55, 0.5);
}

.deaths{
    border-bottom: 6px solid rgba(255, 0, 0, 0.5);
}

@media (max-width:770px){
    .card{
        margin:2% 3% !important;
    }
}